import format from "date-fns/format";
import React, { useContext, useState } from "react";
import { Field, Form } from "react-final-form";
import { Link, useLocation, useNavigate } from "react-router-dom";
import AppointmentService from "../../../services/appointment.services";
import AuthService from "../../../services/auth.service";
import { InputField, FileField, Page } from "common/components";
import AppContext from "store/app-store";

const PatientDetails = () => {
  const { getLang } = useContext(AppContext);
  const title = "Patient Details";
  const required = `Required`;
  const [message, setMessage] = useState();
  const [isLoading, setIsLoading] = useState(false);
  const [isOnBehalf, setIsOnBehalf] = useState();
  const [files, setFiles] = useState();
  const navigate = useNavigate();

  let location = useLocation();
  console.log(
    `PatientDetails :: location: ${JSON.stringify(
      location?.state?.values?.state
    )}`
  );

  const [state, setState] = useState({
    ...location?.state,
  });

  const handleRadioButton = (event) => {
    console.log(`even value: ${event.target.value}`);
    setIsOnBehalf(event.target.value);
  };

  const validate = async (values) => {
    const errors = {};
    if (!values.symptoms) {
      errors.symptoms = required;
    }
    if (!values.isOnBehalf) {
      errors.isOnBehalf = "";
    } else if (values.isOnBehalf === "Yes") {
      if (!values.patientFirstName) errors.patientFirstName = required;
      if (!values.patientLastName) errors.patientLastName = required;
    }
    return errors;
  };
  const getDateMilli = (myDate) => {
    var myDate = new Date("2012-02-10T13:19:11+0000");
    var offset = myDate.getTimezoneOffset() * 60 * 1000;

    var withOffset = myDate.getTime();
    var withoutOffset = withOffset - offset;
    console.log(withOffset);
    console.log(withoutOffset);
  };

  const onSubmit = async (values) => {
    setIsLoading(true);
    console.log(JSON.stringify(values, 0, 2));
    const loggedIn = !!AuthService.getCurrentUser();
    let response;
    if (loggedIn) {
      console.log(
        "ðŸš€ ~ file: index.jsx ~ line 67 ~ onSubmit ~ values.files?.length",
        values.files?.length
      );
      console.log(
        "ðŸš€ ~ file: index.jsx ~ line 67 ~ onSubmit ~ values.files",
        values.files
      );
      if (values.files?.length === 0) {
        response = await AppointmentService.createAppointment(
          AuthService.getCurrentUser().id,
          values?.doctorData?.id,
          values?.appointmentDate,
          "pending",
          getLang()
        );
      } else {
        console.log("files:", files);
        let requestData = new FormData();

        // requestData.append("files", values.files);
        for (let i = 0; i < values.files?.length; i++) {
          requestData.append("files", values.files[i]);
        }
        const request = {
          userId: AuthService.getCurrentUser().id,
          doctorId: values?.doctorData?.id,
          appointmentDate: values?.appointmentDate,
          status: "pending",
        };
        requestData.append("request", JSON.stringify(request, 0, 2));

        response = await AppointmentService.createAppointmentWithFiles(
          requestData,
          getLang()
        );
      }
      setMessage(!!response.message && response.message);
      setIsLoading(false);

      if (!!!response.success && !response.success) return;
      console.log(`response?.body?.id : ${response?.body?.id}`);
      navigate("/" + getLang() + "/medical-success", {
        state: { values, appointmentId: response?.body?.id },
      });
    } else {
      navigate("/" + getLang() + "/login", {
        state: { values, history: "/patient-details" },
      });
    }
  };
  return (
    <Page title={title}>
      <div className="content">
        <div className="container">
          <div className="row patient-details-flex">
            <div className="col-md-7">
              <div className="card">
                <div className="card-body">
                  <Form
                    validate={validate}
                    onSubmit={onSubmit}
                    initialValues={{ ...state }}
                    render={({
                      handleSubmit,
                      submitError,
                      form,
                      submitting,
                      pristine,
                      values,
                    }) => (
                      <form
                        onSubmit={handleSubmit}
                        className="needs-validation"
                        noValidate
                      >
                        {message?.map((m, i) => (
                          <div
                            key={i}
                            className="invalid-feedback d-block mb-2"
                          >
                            {m}
                          </div>
                        ))}
                        <div className="card">
                          <div className="card-body">
                            <h4 className="card-title">Symptoms</h4>
                            <div className="row">
                              <div className="col-md-12 col-sm-12">
                                <div className="form-group card-label">
                                  <Field
                                    name="symptoms"
                                    className="form-control"
                                    type="textarea"
                                    rows="4"
                                    placeholder="Write here symptoms breif..."
                                    component={InputField}
                                  />
                                </div>
                              </div>
                            </div>
                          </div>
                        </div>
                        <div className="card">
                          <div className="card-body">
                            <h4 className="card-title">Attachments</h4>
                            <div className="row">
                              <div className="col-md-12 col-sm-12">
                                <div className="form-group card-label">
                                  <FileField name="files" multiple />
                                </div>
                              </div>
                            </div>
                          </div>
                        </div>
                        <div className="card">
                          <div className="card-body">
                            <h4 className="card-title" hidden>
                              Contact Information
                            </h4>
                            <div className="row" hidden>
                              <div className="col-md-6 col-sm-12">
                                <div className="form-group card-label">
                                  <Field
                                    name="firstName"
                                    className="form-control floating"
                                    label="First Name"
                                    label_type="form_label"
                                    type="text"
                                    component={InputField}
                                  />
                                </div>
                              </div>
                              <div className="col-md-6 col-sm-12">
                                <div className="form-group card-label">
                                  <Field
                                    name="lastName"
                                    className="form-control"
                                    label="Last Name"
                                    label_type="form_label"
                                    type="text"
                                    component={InputField}
                                  />
                                </div>
                              </div>
                              <div className="col-md-6 col-sm-12">
                                <div className="form-group card-label">
                                  <Field
                                    name="email"
                                    className="form-control"
                                    label="Email"
                                    label_type="form_label"
                                    type="text"
                                    component={InputField}
                                  />
                                </div>
                              </div>
                              <div className="col-md-6 col-sm-12">
                                <div className="form-group card-label">
                                  <Field
                                    name="mobile"
                                    className="form-control"
                                    label="mobile"
                                    label_type="form_label"
                                    type="text"
                                    component={InputField}
                                  />
                                </div>
                              </div>
                            </div>
                            <div className="form-group row">
                              <h4 className="col-lg-12 card-title">
                                Are you booking for someone else?
                              </h4>
                              <div className="invalid-feedback d-block mt-n2 mx-3">
                                {`${!!!isOnBehalf ? required : ""}`}
                              </div>
                              <div className="col-lg-12 mt-2">
                                <div className="form-check form-check-inline">
                                  <Field
                                    name="isOnBehalf"
                                    component={InputField}
                                    type="radio"
                                    id="no_radio"
                                    className="form-check-input form-check-inline"
                                    value="No"
                                    checked={isOnBehalf === "No"}
                                    onClick={handleRadioButton}
                                  />
                                  <label
                                    htmlFor="no_radio"
                                    className="form-check-label"
                                  >
                                    No
                                  </label>
                                </div>
                                <br />
                                <div className="form-check form-check-inline">
                                  <Field
                                    id="yes_radio"
                                    className="form-check-input form-check-inline"
                                    checked={isOnBehalf === "Yes"}
                                    name="isOnBehalf"
                                    value="Yes"
                                    type="radio"
                                    component={InputField}
                                    onClick={handleRadioButton}
                                  />
                                  <label
                                    htmlFor="yes_radio"
                                    className="form-check-label"
                                  >
                                    Yes (Please enter patient's info below)
                                  </label>
                                </div>
                              </div>
                            </div>
                            {/* <div className="exist-customer">Existing Customer? <a href="/login.html">Click here to login</a></div> */}
                          </div>
                        </div>

                        <div
                          className="card"
                          hidden={isOnBehalf === "No" || !!!isOnBehalf}
                        >
                          <div className="card-body">
                            <h4 className="card-title">On behalf of</h4>
                            <div className="row">
                              <div className="col-md-6 col-sm-12">
                                <div className="form-group card-label">
                                  <Field
                                    name="patientFirstName"
                                    className="form-control"
                                    type="text"
                                    label="First Name"
                                    label_type="form_label"
                                    component={InputField}
                                  />
                                </div>
                              </div>
                              <div className="col-md-6 col-sm-12">
                                <div className="form-group card-label">
                                  <Field
                                    name="patientLastName"
                                    className="form-control"
                                    type="text"
                                    label="Last Name"
                                    label_type="form_label"
                                    component={InputField}
                                  />
                                </div>
                              </div>
                            </div>
                          </div>
                        </div>
                        <div className="submit-section d-flex justify-content-between">
                          <Link to={`/doctor-profile`} state={{ ...state }}>
                            <button
                              className="btn btn-primary submit-btn"
                              type="button"
                              name="form_submit"
                              disabled={isLoading}
                            >
                              Back
                            </button>
                          </Link>
                          <button
                            className="btn btn-primary submit-btn"
                            type="submit"
                            name="form_submit"
                            value="submit"
                            disabled={isLoading}
                          >
                            {!isLoading ? `Confirm` : ""}
                            {isLoading && (
                              <span className="spinner-border spinner-border-sm mb-1"></span>
                            )}
                          </button>
                        </div>
                        {/* <pre>{JSON.stringify(values, 0, 2)}</pre> */}
                      </form>
                    )}
                  />
                </div>
              </div>
            </div>

            <div className="col-md-5 col-lg-4 theiaStickySidebar">
              {/* Booking Summary */}
              <div className="card booking-card">
                <div className="card-header">
                  <h4 className="card-title">Booking Summary</h4>
                  <h4 className="card-title">{JSON.stringify(state)}</h4>
                </div>
                <div className="card-body">
                  <div className="booking-doc-info">
                    <img
                      src={state?.doctorData?.image}
                      className="booking-doc-img"
                      alt="Doctor"
                    />
                    <div className="booking-info">
                      <h4>{state?.doctorData?.doc_name}</h4>
                      <div className="clinic-details">
                        <p className="doc-location">
                          <i className="fas fa-map-marker-alt"></i>{" "}
                          {state?.doctorData?.address}
                        </p>
                      </div>
                    </div>
                  </div>
                  <div className="booking-summary">
                    <div className="booking-item-wrap">
                      <ul className="booking-date p-2">
                        <li>
                          Approximate Date{" "}
                          <span>
                            {state?.appointmentDate &&
                              format(
                                new Date(state?.appointmentDate),
                                "MM/dd/yyyy"
                              )}
                          </span>
                        </li>
                      </ul>
                    </div>
                  </div>
                </div>
              </div>
              {/* Booking Summary */}
            </div>
          </div>
        </div>
      </div>
    </Page>
  );
};
export default PatientDetails;
